
.message {
    position: absolute;
    top: calc(50% - 40px);
    -webkit-transform: translate(0, -50%);
        -ms-transform: translate(0, -50%);
            transform: translate(0, -50%);
    background: rgb(0, 242, 250);
    height: 50px;
    -webkit-transition: 4s;
    -o-transition: 4s;
    transition: 4s;
}

.message-enter {
    opacity: 0.01;
    width: 0px;
}

.message-enter.message-enter-active {
opacity: 1;
width: 100px;
-webkit-transition: 700ms;
-o-transition: 700ms;
transition: 700ms;
}

.message-leave {
    opacity: 1;
    width: 100px;
}

.message-leave.message-leave-active {
opacity: 0.01;
width: 0px;
-webkit-transition: 700ms;
-o-transition: 700ms;
transition: 700ms;
}

#right {
    left: 0;
    border-radius: 0 25px 25px 0;
}

#left {
    right: 0;
    border-radius: 25px 0 0 25px;
}




